body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

*{box-sizing:border-box;font-family:Raleway, sans-serif;font-weight:700;margin:0;padding:0}body,html{background-image:url(/static/media/wood.292c7e33.png);background-repeat:no-repeat;background-size:cover;width:100%;overflow:hidden;background-clip:border-box}h1{font-family:'Great Vibes', cursive;font-size:12vh;color:black;letter-spacing:3px;opacity:.7;margin-bottom:0vh;margin-top:-2vh;-webkit-text-stroke:2px black}input{box-shadow:inset 0 0 2px;width:30vw;height:5vh;text-align:center;font-weight:700;font-size:medium;background-color:#dbdb96}input:focus{outline:none}.buttonsContainer{-webkit-perspective:200px;perspective:200px;margin-bottom:-2vh}.btn{margin:2vh 2vw;width:5vw;min-width:34px;height:5vh;min-height:34px;border:2px solid #1d1d1d;background-color:#006bd6;opacity:.8;color:#dbdb96;font-size:30px;border-radius:6px;cursor:pointer;transition:.4s ease-in;box-shadow:6px 4px 6px black}.btn1:hover{transition:translateY();color:#dbdb96;background-color:dodgerblue;box-shadow:15px 10px 15px black}.btn2{background-color:#a00000;color:black}.btn2:hover{-webkit-transform:translateY();transform:translateY();color:#dbdb96;background-color:#db0000;box-shadow:15px 10px 15px black}.wrapper{height:100vh}.header{text-align:center;height:10vh;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;display:table;margin-right:auto;margin-left:auto;margin-bottom:3vh}.cards-table{width:94vw;height:70vh;-webkit-align-items:center;align-items:center;margin:auto;display:-webkit-flex;display:flex;-webkit-flex-wrap:wrap;flex-wrap:wrap;-webkit-flex-direction:row;flex-direction:row;-webkit-align-content:center;align-content:center;display:grid;grid-template:repeat(6, 1fr)/repeat(6, 1fr);grid-gap:2vw;gap:2vw}.card{margin:auto;height:13vh;width:26vh;margin:auto;cursor:pointer;box-shadow:25px 20px 25px black;border-radius:5px;border:2px solid #0c0c0c;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;background-image:url(/static/media/wildlife.15c32fbf.jpg);background-size:cover}.card-translation{height:13vh;width:26vh;background:#F3F6B0;background:radial-gradient(ellipse at center, #F3F6B0, #7C671F);background-size:cover;margin:auto;box-shadow:25px 20px 25px black;border-radius:5px;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;opacity:1;-webkit-align-items:center;align-items:center;border:2px solid #0c0c0c}

